import { PreorderSettings, PreorderStatus } from '~common/types';
import { Variant } from '~product-details/ShopifyOptionSelectors';
import { tryParseJsonString, LocationApi, LogFn } from '~utils/common/common';
import { PreorderError } from '~utils/common/errors';
import { CartForm } from './CartForm';
import {
    VariantApiJson,
    CartPageApiService,
    CartSettings,
} from './services/CartPageApiService';
import { CartData } from './types';

// declare global {
//  const ShopifyConst: { shop: string, OptionSelectors: any };
// }
type CustomProps = { 
  url: string,
  method: string,
  headers: {[key: string]: string},
};
type PatchedXMLHttpRequest = XMLHttpRequest & { __meta_params: CustomProps };
interface GetChangeCartDataReturnType {
  variant: {
      id: number;
      quantity: number;
  } | undefined;
  newVariantQuantity: number;
  currentVariantQuantity: number | null;
  newChangeParams: {
      line?: string;
      id?: string;
  };
}

type GetChangeCartDataHandler = (bodyJson: {[key in 'quantity' | 'id' | 'line']: string}, body: BodyInit, cartData: CartData) => Promise<GetChangeCartDataReturnType>;
export class CartPageController {
    cartForm!: CartForm;
    product: VariantApiJson | null = null;
    settings: PreorderSettings | null = null;
    // shopifyOptionSelectors!: ShopifyOptionSelectors;

    private selectedVariant: Variant | null = null;
    private urlHandlers = new Map<RegExp, GetChangeCartDataHandler>();

    protected addUrlHandler(urlRegExp: RegExp, urlHandler: GetChangeCartDataHandler) {
      this.urlHandlers.set(urlRegExp, urlHandler);
    }
    
    constructor(
        protected log: LogFn,
        protected cartApiService: CartPageApiService,
        protected cartSettings: CartSettings,
    ) {
        this.log = log.bind(null, CartPageController.name, ':: ');
        this.cartForm = new CartForm();
        this.addUrlHandler(/\/cart\/change.js$/, CartPageController.getChangeCartData);
    }

   init() {    
        if (this.cartForm.isExist) {
            this.log('cart form is on the page');
            this.patchCartChangeRequest();
        }
        //  else {
        //     const error = 'cart form is not on the page';
        //     this.log(error);
        //     throw new PreorderError(error);
        // }
    }
   
    protected async processBody(body: BodyInit, url: string, headers?:  CustomProps["headers"]): Promise<BodyInit | null> {
      let newBody: BodyInit | null = body;
      
      for (const [urlRegExp, getChangeCartData] of this.urlHandlers.entries()) {
        const isCartChangeRequest = urlRegExp.test(url);
        
        if(isCartChangeRequest) {
          const cartData: CartData = await fetch('/cart.js').then(r => r.json());
          let bodyJson = tryParseJsonString(body as string);
          let isJsonString = false;
          
          if (bodyJson !== undefined) {
            isJsonString = true;
          }
          
          if(typeof body === "string") {
             const contentType = headers?.["Content-Type"] ?? '';
             
             if (contentType.includes("application/x-www-form-urlencoded")) {
                body = new URLSearchParams(body);
             }
          }
          
          if(body instanceof FormData || body instanceof URLSearchParams) {
            bodyJson = Object.fromEntries(body.entries()) as { quantity: string, id: string};
          }
          
          const { variant, newVariantQuantity, currentVariantQuantity, newChangeParams } =  await getChangeCartData(bodyJson, body, cartData);
          
            
          if (variant) {
            const variantData = await this.cartApiService.loadVariantData(String(variant.id));

            if (variantData) {
              if (newVariantQuantity > variantData.allowed_qnty) {
                const newBodyJson = {
                  quantity: String(variantData.allowed_qnty),
                  ...newChangeParams,
                };

                
                if(body instanceof FormData) {
                  newBody = null;
                } else {
                  newBody = isJsonString ? JSON.stringify(newBodyJson) : new URLSearchParams(newBodyJson).toString();
                }
                
                const alertMessage = this.cartSettings.forbidding_addition_message.replace('${allowed_qnty}', String(variantData.allowed_qnty));
                alert(alertMessage);

              }
            }
          }
        }
      }
       
      
      return newBody;
    }
    
    protected patchCartChangeRequest() {
      const controller = this;
      
      (function (originalSend) {
        XMLHttpRequest.prototype.send = async function (body: BodyInit) {
          const xhr: PatchedXMLHttpRequest = this as any;
          console.log('send start -> context: ', this); 
          
          const newBody = await controller.processBody(body, xhr.__meta_params?.url, xhr.__meta_params?.headers);
          
          if (newBody) {
            originalSend.call(xhr, newBody);
          }
          
        };
      })(XMLHttpRequest.prototype.send);
    
      (function (originalOpen) { 
          type ajaxT = typeof XMLHttpRequest;
          type ArgsType = Parameters<(ajaxT)["prototype"]["open"]>;
          XMLHttpRequest.prototype.open = function () {
            const args = arguments as unknown as ArgsType;
            const xhr: PatchedXMLHttpRequest = this as any;
            
            xhr.__meta_params = xhr.__meta_params ?? {};
            
            xhr.__meta_params = {
              ...xhr.__meta_params,
              method: args[0],
              url: args[1],
            };
            
            
            originalOpen.call(xhr, ...args);
          };
      })(XMLHttpRequest.prototype.open);
      
      (function (originalSetRequestHeader) { 
          type ajaxT = typeof XMLHttpRequest;
          type ArgsType = Parameters<(ajaxT)["prototype"]["setRequestHeader"]>;
          XMLHttpRequest.prototype.setRequestHeader = function () {
            const args = arguments as unknown as ArgsType;
            const xhr: PatchedXMLHttpRequest = this as any;
            
            xhr.__meta_params = xhr.__meta_params ?? { headers: {} };
            
            xhr.__meta_params.headers = {
              ...xhr.__meta_params.headers,
               [args[0]]: args[1],
            };
            
            
            originalSetRequestHeader.call(xhr, ...args);
          };
      })(XMLHttpRequest.prototype.setRequestHeader);    
      
      
      (function (originalFetch) {
          type ArgsType = Parameters<typeof window.fetch>;
          window.fetch = async function (...args: ArgsType) {
            
            const [ requestInfo, requestInit]  = args;
            let url = requestInfo as string;
            let body = requestInit?.body;
            
            if (requestInfo instanceof Request) {
              url = requestInfo.url;
            }
            else if (typeof requestInfo !== 'string') {
              url = String(requestInfo);
            }
            
            if (body) {
              
              const newBody = await controller.processBody(body, url);
              if (newBody !== body) {
                requestInit!.body = newBody;
              }
              
              if (requestInit!.body) {
                return originalFetch(...args);
               } else {
                 return Promise.reject({
                   json: () => Promise.resolve({})
                 })
               }
            }
            
            return originalFetch(...args);
          };
      })(window.fetch);
      
    }

    fallbackForCatchError = ((error: any) => {
        this.cartForm.hideSpinner();
        console.error('LOAD ERROR', error);
        this.cartForm.showThemesButtons();
    }).bind(this);

    async loadVariantData(id: number) {
        const variantData = await this.cartApiService.loadVariantData(String(id));

        return variantData;
    }
    
    static async getChangeCartData(changeParams: { quantity: string, line: string}, body: BodyInit, cartData: CartData): Promise<GetChangeCartDataReturnType> {
        const variantIndex = Number(changeParams.line) - 1;
        const newVariantQuantity = Number(changeParams.quantity);
        
        console.log('cartData', cartData);
        const variant = cartData.items[variantIndex];
        const currentVariantQuantity = variant?.quantity ?? 0;

        return { variant, newVariantQuantity, currentVariantQuantity, newChangeParams: { line: changeParams.line, } };
    }

    async loadSettings(selectedVariant?: Variant) {
        this.cartForm.showSpinner();

        if (selectedVariant) {
            this.selectedVariant = selectedVariant;
        }

        await this.getSettings(this.selectedVariant?.id);

        this.cartForm.hideSpinner();

        if (
            this.settings?.variant.preorder_status === PreorderStatus.Activated
        ) {
            this.cartForm.appendPreorderButton();
            this.cartForm.displayPreorderButton();
        } else {
            this.cartForm.showThemesButtons();
        }
    }

    async getSettings(variant_id?: number) {
        if (this.product === null && !variant_id) {
            return await Promise.resolve(null);
        }

        const settings = await this.cartApiService.loadSettings(
            window.Shopify.shop,
            String(variant_id!)
        );

        this.cartForm.setPreorderButtonSettings({
            text: settings?.button.text ?? '',
            message: settings?.button.message ?? '',
            design_settings: settings?.button.design_settings.current.preorder!,
        });

        this.cartForm.preorderButton.setAavailabilityDate(
            settings?.variant.availability_date
        );

        this.settings = {
            ...this.settings,
            variant: settings?.variant!,
        };
    }
}

export class ProductPageCartController extends CartPageController {
  
  constructor(
      log: LogFn,
      cartApiService: CartPageApiService,
      cartSettings: CartSettings,
  ) {
    super(log, cartApiService, cartSettings);
    this.log = log.bind(null, ProductPageCartController.name, ':: ');
    this.addUrlHandler(/\/cart\/add.js$/, ProductPageCartController.getAddCartData);
  }
  
  init() {    
    super.patchCartChangeRequest();
  }
  
  static async getAddCartData(addParams: { quantity: string, id: string}, body: BodyInit, cartData: CartData): Promise<GetChangeCartDataReturnType>  {
    
    
    console.log('getChangeCartData ::cartData', cartData);
    const variant = cartData.items.find((item) => item.id === Number(addParams.id));
    const currentVariantQuantity = (variant?.quantity ?? null);
    let newVariantQuantity = Number(addParams.quantity) + (variant?.quantity ?? 0);
    
    if(body instanceof FormData && variant) {
      newVariantQuantity = variant.quantity + 1;
    }
    
    return { variant, newVariantQuantity, currentVariantQuantity, newChangeParams: { id: addParams.id, } };
  }
  
  
}
