    selectors: any;
    displayOriginValue = 'block';
    themesButtons: ThemesButtons = { items: new Map(), visible: false, styleElement: null };
    themesButtonsWidth: string | null = null;
    preorderButton!: PreorderButton;
    settings: ButtonSettings | null = null;
    spinner: PreorderSpinner;

