    onClick(cb: (e: MouseEvent) => void) {
        this.element?.addEventListener('click', cb);
    }

    onChange(cb: (e: Event) => void) {
        this.element?.addEventListener('change', cb);
    }

    onVariantChange(cb: (e: Event) => void) {
        this.element?.addEventListener('change', e => cb(e));
    }

    // onSelectProductVariant(cb: (e: Event) => void) {
    //     this.variantsSelectElement?.addEventListener('change', cb);
    // }


    setThemesButtons() {
        if (this.element !== null) {
            selectors.product.button.forEach((selector) => {
                const element = this.element?.querySelector<HTMLButtonElement>(
                    selector
                );
                // if (element) {
                selector = `${selectors.product.form && ''} ${selector}`;
                if (!this.themesButtons.items.has(selector)) {
                    this.themesButtons.items.set(selector, {
                        ref: element ?? null,
                        style: element ? window.getComputedStyle(element).display : null,
                        selector: `${selectors.product.form && ''} ${selector}`
                    });
                }
                // }
            });
        }
    }

    get isExist() {
        return Boolean(this.element);
    }

    appendPreorderButton() {
        this.element?.append(this.preorderButton.element);
    }

    displayPreorderButton() {
        this.preorderButton.display();
    }

    showThemesButtons() {
        this.themesButtons.items.forEach((element) => {
            // applyStyle(element.style, element.ref);
            if (element?.ref && element.style) {
                // elementToHide!.ref!.style!.display = 'none';
                element.ref.style.display = element.style;
            }
        });
        // this.removeThemesButtonsStyle();
        this.themesButtons.visible = true;
    }

    hideThemesButtons() {
        // const styles = [...this.themesButtons.items].map(({ selector }) => {
        //     return { selector, rules: 'display: none !important;' };
        // });
        // this.createThemesButtonsStyle(styles);
        this.setThemesButtons();

        this.themesButtons.items.forEach((elementToHide) => {
            // elementToHide.ref.setAttribute('style', 'display: none');
            if (elementToHide?.ref) {
                elementToHide!.ref!.style!.display = 'none';
                if (this.themesButtonsWidth === null) {
                    this.themesButtonsWidth = window.getComputedStyle(elementToHide.ref).width;
                }
            }
        });

        if (this.themesButtonsWidth) {
            this.preorderButton.element.style.width = this.themesButtonsWidth;
        }

        this.themesButtons.visible = false;
    }

    async showSpinner() {
        await sleep();
        this.hideThemesButtons();
        this.preorderButton.hide();

        // this.spinner = new PreorderSpinner();
        // this.element?.append(this.spinner.element);
        this.spinner.display(this.themesButtonsWidth);
    }

    hideSpinner() {
        // this.spinner?.element.parentElement?.removeChild(this.spinner?.element);
        this.hideThemesButtons();
        this.spinner.hide();
    }

    setPreorderButtonSettings(settings: ButtonSettings) {
        this.settings = settings;
        this.preorderButton.setSettings(settings.design_settings);
        this.preorderButton.setText(settings.text);
        this.preorderButton.setMessage(settings.message);
    }

    triggerVariantChangeSync() {
        this.element?.querySelector('select')?.dispatchEvent(new Event('change', { 'bubbles': true }));
    }

    getSelectedVariantId() {
        return this.variantsSelectElement?.value;
    }

